@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.skill-container {
  /* justify-content: center; */
  /* align-items: center; */
  width: 100%;
  display: flex;
  flex-direction: column;
  /* padding: 5rem; */
  /* transition: 500ms; */
}

.skill__level {
  background-color: aqua;
  width: 100%;
  height: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 5rem; */
  /* transition: 500ms; */
}

.skill-container__title {
  padding-bottom: 5%;
  width: 100%;

  text-align: center;
  color: #686f85;
  padding-left: 10px;
  /* font-family:'Courier New', Courier, monospace; */
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  /* font-family: "Red Hat Display", sans-serif; */
  font-weight: 900;
  font-size: 40px;
}

.skill-container__Cards {
  display: flex;
  justify-content: space-between;
}

.skills__Card {
  padding: 2rem;
  box-shadow: 0px 0px 10px rgb(1, 1, 12);
  backdrop-filter: blur(10);
  background: var(--light-grad-pink-blue, radial-gradient(ellipse at 40% 0%, #bf398910 0, transparent 75%), radial-gradient(ellipse at 60% 0%, #096bde10 0, transparent 75%));
  filter: contrast(120%);
  border-radius: 10px;
  border-radius: 10px;
  


  margin: 10px;
  color: black;
}

.skills__title {
  color: aliceblue;
  font-size: 60%;
  padding-left: 2px;
}

.skill_grid {
  display: flex;
  flex-wrap: wrap;
  flex: 0 0 calc(33.33% - 20px);
  background: transparent;
}

.skill_name {
  border-style: solid;
  border-color: transparent;
  border-width: 1px;
  color: #bec4d696;
  font-weight: 500;
  background: var(--light-grad-pink-blue, radial-gradient(ellipse at 40% 0%, #bf398910 0, transparent 100%), radial-gradient(ellipse at 60% 0%, #096bde10 0, transparent 100%));

  filter: contrast(120%);
  padding: 5px;
  border-radius: 5px;
  margin: 3px;
}

@media screen and (max-width: 1062px) {
  .skill-container__Cards {
    flex-direction: column;
    transition-property: all;

  }


}


