@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.About {
    display: flex;
    /* padding-left: 5%; */
    /* padding-right: 5%; */
    align-items: center;
    padding-bottom: 5rem;


}

.intro {
    /* width: 100%; */
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* justify-content: center; */
    /* padding-top: 10px; */

}

.intro__greeting {
    width: 75%;
    color: #ccd6f6;
    padding-top: 40px;
    /* width: 100px; */
    /* width: 20%; */
    margin-top: 0px;
    margin-bottom: 0px;
    /* color:#4c8ce6; */
    /* font-family: "Montserrat',sans-serif"; */
    font-family: Helvetica, sans-serif;
    /* font-family: 'Poppins', sans-serif; */
}

.intro__name {
    width: max-content;
    opacity: 1;
    font-family: "Montserrat',sans-serif";
    margin-top: 0px;
    margin-bottom: 0px;
    /* font-size:100%; */
    color: #4c8ce6;
    font-size: 500%;
}

.intro__tagline {
    /* line-gap-override: 2px; */

    width: 75%;
    /* text-align: justify; */
    margin: 0px;
    font-size: 500%;
    color: #686f85;
    font-family: 'Poppins', sans-serif;
}

.intro__description {

    text-align: justify;
    width: 75%;
    color: #42485a;
    /* opacity: .7; */
    font-family: 'Poppins', sans-serif;
    font-size: 120%;

}

.next-page__arrow {
    color: #4c8ce6;
    animation: bouncing 1.5s infinite ease-in-out;
    bottom: 0;
    display: block;
    height: 90px;
    left: 90%;
    margin-left: -25px;
    position: absolute;
    width: 90px;
    transition: 200ms;
}

.arrow :hover {
    width: 90px;
    color: #ccd6f6;


}

@keyframes bouncing {
    0% {
        bottom: 0px;
    }

    50% {
        bottom: 20px;
    }

    100% {
        bottom: 0px;
    }
}

.location {
    color: #ccd6f6;
    width: fit-content;
}

.About__sticker {
    /* filter: invert(); */
    /* width according to neighber div */
    /* width: 20vmax; */
    /* size: 20px; */
    /* aspect-ratio: 1/.5; */
    /* height: 10px; */
    /* height: min-content; */
    filter: contrast(90%) brightness(30%);
}

.About__sticker {
    /* filter: invert(); */
    /* width according to neighber div */
    /* width: 25vmax;
    filter: contrast(90%) brightness(30%); */
}

.intro__container__icon {
    margin-top: 50px;
    display: flex;
    gap:5px;
    width: fit-content;
    color: #4c8ce6;
    /* padding: 10px; */
}

/* logo in on row  with padding and hover*/
.intro__icon {
    font-size: 2vmin;
    align-items: center;
    font-weight: 900;
    position: relative;
    padding: 10px;
}

.intro__icon:hover {
    color: #ccd6f6;
    animation-duration: 100ms;
    transition: 500ms;
    transform: scale(1.1);
}

/* responsiveness for dive About */

@media (max-width: 900px) {
    .About__sticker {
        display: none;
    }

    .About {
        margin-top: 25%;
        width: fit-content;
        align-items: center;
        align-content: center;
    }

    .intro {
        gap: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        /* width: 10%; */
    }

    .intro__name {
        /* width: 100%; */
        letter-spacing: 3px;
        padding-top: 2%;
        font-size: 10vmin;
    }

    .intro__tagline {
        font-size: 2rem;
        word-spacing: 5px;
        /* letter-spacing: 2px; */
    }

    .temp_about {
        width: min-content;
        /* display: none; */
        width: fit-content;
    }

    .intro__description {
        font-size: 1rem;
    }

    .intro__container__icon {
        gap: 5px;
    }

    .intro__icon {
        font-size: 2rem;
    }

}